<template>
    <div class="auth">
        <div class="warp" v-show="ismask">
            <div class="main">
                <van-col span="24" v-show="is_card">
                    <div class="upload">
                        <van-uploader :after-read="onRead" class="read" enctype="multipart/form-data">
                            <van-button size="large" style="background:#f0f0f0;color:#999;border:none"></van-button>
                        </van-uploader>
                    </div>
                </van-col>
                <van-col span="24" style="border-bottom:1px solid #eee">
                    <van-col span="6" style="text-align:left">
                        <span class="left"></span>
                    </van-col>
                    <van-col span="18" style="text-align:right" class="name">
                        <van-field placeholder="" v-model="realname" class="realname right" style="text-align: right;"/>
                    </van-col>
                </van-col>
                <van-col span="24" style="border-bottom:1px solid #eee">
                    <van-col span="6" style="text-align:left">
                        <span class="left"></span>
                    </van-col>
                    <van-col span="18" style="text-align:right" class="cardid">
                        <van-field type="number" placeholder="" v-model="idcard_num" class="realid right" style="text-align: right;"/>
                    </van-col>
                </van-col>
                <van-col span="24" v-show="is_bank">
                    <div class="upload">
                        <van-uploader :after-read="onread" class="read" enctype="multipart/form-data">
                            <van-button size="large" style="background:#f0f0f0;color:#999;border:none"></van-button>
                        </van-uploader>
                    </div>
                </van-col>
                <van-col span="24" style="border-bottom:1px solid #eee">
                    <van-col span="6" style="text-align:left">
                        <span class="left"></span>
                    </van-col>
                    <van-col span="18" style="text-align:right" class="bankid">
                        <van-field type="number" placeholder="" v-model="bankcard" class="banid right" style="text-align: right;"/>
                    </van-col>
                </van-col>
                <van-col span="24" style="border-bottom:1px solid #eee">
                    <van-col span="6" style="text-align:left">
                        <span class="left"></span>
                    </van-col>
                    <van-col span="18" style="text-align:right" class="bank">
                        <van-field placeholder="" disabled v-model="bank" class="bank right" style="" right-icon="arrow-down" @click-right-icon="Fbank" style="color:#000;"/>
                    </van-col>
                </van-col>
                <van-col span="24" style="border-bottom:1px solid #eee">
                    <van-col span="6" style="text-align:left">
                        <span class="left"></span>
                    </van-col>
                    <van-col span="18" style="text-align:right;height:auto" class="openbank">
                        <van-field placeholder="" class="bank right" v-model="openbank" right-icon="arrow-down" @click-right-icon="select"/>
                    </van-col>
                </van-col>
                <van-col span="24">
                    <div class="tip">
                        <p class="title"></p>
                        <p class="msg">1.</p>
                        <p class="msg">2.</p>
                        <p class="msg">3.</p>
                    </div>
                    <div class="up" style="text-align:center">
                        <van-uploader :after-read="Onread" style="margin:0 auto">
                            <div class="bg"></div>
                        </van-uploader>
                    </div>
                </van-col>
            </div>
        </div>
        <div class="btn" v-show="ismask">
            <van-button round :type="btnType" size="large" :disabled="isdisable" @click="submit" style="color:#fff;background:#00d094;boarder:none">{{btnTxt}}</van-button>
        </div>
        <van-popup position="bottom" v-model="cityshow">
            <van-area :area-list="areaList" columns-num="2" @confirm="confirm"	@cancel="cancel" />
        </van-popup>
        <van-popup position="bottom" v-model="open">
            <van-picker
                show-toolbar
                title=""
                :columns="columns"
                @cancel="onCancel"
                @confirm="onConfirm"
            />
        </van-popup>
        <van-popup position="bottom" v-model="isbank">
            <van-picker
                show-toolbar
                title=""
                :columns="banks"
                @cancel="closebank"
                @confirm="onbank"
            />
        </van-popup>
    </div>
</template>

<script>
WebApp.InitUpload();
WebApp.JSAPI.InitShare({
    title:'',
    desc:"",
    link:'http://tinydream.ivkcld.cn/TinydreamWeb/dream/dist/html/share.html?time='+new Date().getTime()+'&type=dream&state=no',
    imgUrl:"https://tdream.antit.top/image/miniLogo.jpg"
});
import areaList from 'vant/packages/area/demo/area.js'
import { ImagePreview } from 'vant';
export default {
    name:'auth',
    data () {
        return {
            ismask:false,//
            realname:'',//
            idcard_num:'',//
            bankcard:'',//
            bank:'',//
            openbank:'',//
            is_card:false,//
            is_bank:false,//
            areaList:areaList,
            cityshow:false,
            columns:'',//
            banks:["","","","",
"","","","","","",
"","","","","","",
"","","","","","",
"","","","","","",
"","","","","","",
"","","","","","",
"","","","","","",
"","","","","","",
"","","","","",
"","","","","","",
"","","","","","",
"","","","","","",
"","","","","","",
"","","","","","",
"","","","","",
"","","","",
"","","","",
"","",""],//
            open:false,
            isbank:false,
            fileObj:'',//
            uploadData:'',//
            isdisable:false,//
            btnType:'primary',//
            btnTxt:'',//
            src:'',//
        }
    },
    created(){
        this.isauth(this)
    },
    methods:{
        onRead(file){
            var app = this;
            console.log(file)
            var fromdata = new FormData();
            fromdata.append('file',file.file)
            this.$toast.loading({
                duration:0,
                forbidClick:true,
                loadingType:'circular',
                message:'...'
            })
            $.ajax({
                url:'http://tinydream.ivkcld.cn/TinydreamWeb/dream/dist/php/token.php',
                data:fromdata,
                type:'post',
                processData:false,
                contentType:false,
                success:function(data){
                    console.log(data);
                    var obj = typeof data == 'object' ? data : eval("("+data+")");
                    if(obj.image_status == 'normal'){
                        app.realname = obj.words_result['']['words'];
                        app.idcard_num = obj.words_result['']['words'];
                        app.$toast.clear();
                    }else if(obj.image_status == 'non_idcard'){
                        app.$toast.clear();
                        app.$dialog.alert({
                            title:'',
                            message:''
                        }).then(()=>{
                            app.$router.replace('/refesh')
                        })
                    }else if(obj.image_status == 'reversed_side'){
                        app.$dialog.alert({
                            title:'',
                            message:''
                        }).then(()=>{
                            app.$router.replace('/refesh')
                        })
                    }else if(obj.image_status == 'blurred'){
                        app.$dialog.alert({
                            title:'',
                            message:''
                        }).then(()=>{
                            app.$router.replace('/refesh')
                        })
                    }else if(obj.image_status == 'other_type_card'){
                        app.$dialog.alert({
                            title:'',
                            message:''
                        }).then(()=>{
                            app.$router.replace('/refesh')
                        })
                    }else if(obj.image_status == 'over_exposure'){
                        app.$dialog.alert({
                            title:'',
                            message:''
                        }).then(()=>{
                            app.$router.replace('/refesh')
                        })
                    }
                }
            })
        },
        onread(file){
            console.log(file)
            var app = this;
            console.log(file)
            var fromdata = new FormData();
            fromdata.append('file',file.file)
            this.$toast.loading({
                duration:0,
                forbidClick:true,
                loadingType:'circular',
                message:'...'
            })
            $.ajax({
                url:'http://tinydream.ivkcld.cn/TinydreamWeb/dream/dist/php/bank.php',
                data:fromdata,
                type:'post',
                processData:false,
                contentType:false,
                success:function(data){
                    console.log(data);
                    var obj = typeof data == 'object' ? data : eval("("+data+")");
                    console.log(obj)
                    app.bankcard = obj.result.bank_card_number
                    app.bank = obj.result.bank_name
                    app.$toast.clear();
                }
            })
        },
        Onread(file){
            console.log(file)
            this.fileObj = file.file;
            console.log(file.file,this.fileObj);
            this.src = file.content;
            $('.up').html('<div class="bg" style="width: 4.3rem;height: 3.26rem;margin: 0 auto;background: url('+file.content+') no-repeat center center / 4.3rem 3.26rem;"></div>').click(function(){
                ImagePreview([file.content])
            });
        },
        // 
        select(){
            if(this.bank != ''){
                this.cityshow = true;
            }else{
                this.$dialog.alert({
                    title:'',
                    message:''
                })
            } 
        },
        // 
        Fbank(){
            if(this.bankcard != ''){
                this.isbank = true;
            }else{
                this.$dialog.alert({
                    title:'',
                    message:''
                })
            }
        },
        // 
        confirm(data){
            console.log(data);
            var app = this;
            $.post('http://tinydream.ivkcld.cn/TinydreamWeb/dream/dist/php/address.php',{bank:app.bank,city:data[1].name},function(data){
                console.log(data);
                var dataObj = typeof data == 'object' ? data : eval("("+data+")");
                var openbank = [];
                $.each(dataObj.results,function(index,item){
                    var address = item.address + item.name;
                    openbank.push(address);
                })
                app.columns = openbank;
            })
            this.cityshow = false;
            this.open = true;
        },
        cancel(){
            this.cityshow = false;
        },
        // 
        onConfirm(item,index){   
            this.openbank = item;
            this.open = false;
        },
        onCancel(index){
            this.open = false;
        },
        // 
        onbank(item,index){
            console.log(index)
            this.bank = item;
            this.isbank = false;
        },
        closebank(index){
            this.isbank = false;
        },
        // 
        isauth(app){
            TD_Request('us','rnamegx',{uid:app.$store.state.uid},function(code,data){
                console.log(data)
                if(data.realName[app.$store.state.uid].state == 'NONE'){
                    app.btnTxt = '';
                    app.btnType = 'default';
                    app.is_card = true;
                    app.is_bank = true;
                    app.ismask = true;
                }else if(data.realName[app.$store.state.uid].state == 'SUBMIT'){
                    app.btnTxt = '';
                    app.btnType = 'warning';
                    app.isdisable = true;
                    app.is_card = false;
                    app.is_bank = false;
                    $('.name').html('<span>'+data['realName'][app.$store.state.uid].realname+'</span>').css({
                        display:'inline-block',
                        height:'0.88rem',
                        'line-height':'0.88rem'
                    });
                    $('.cardid').html('<span>'+data['realName'][app.$store.state.uid].icardnum+'</span>').css({
                        display:'inline-block',
                        height:'0.88rem',
                        'line-height':'0.88rem'
                    });
                    $('.bankid').html('<span>'+data['realName'][app.$store.state.uid].ccardnum+'</span>').css({
                        display:'inline-block',
                        height:'0.88rem',
                        'line-height':'0.88rem'
                    });
                    $('.bank').html('<span>'+data['realName'][app.$store.state.uid].bank+'</span>').css({
                        display:'inline-block',
                        height:'0.88rem',
                        'line-height':'0.88rem'
                    });
                    $('.openbank').html('<span>'+data['realName'][app.$store.state.uid].openbank+'</span>').css({
                        display:'inline-block',
                        height:'auto',
                        'line-height':'1.5',
                        paddint:'0.2rem 0 0',
                        'text-align':'left'
                    });
                    $('.up').html('<div class="bg" style="width: 4.3rem;height: 3.26rem;margin: 0 auto;background: url('+data['realName'][app.$store.state.uid].icardfurl+') no-repeat center center / 4.3rem 3.26rem;"></div>').click(function(){
                        ImagePreview([data['realName'][app.$store.state.uid].icardfurl])
                    });
                    app.ismask = true;
                }else if(data.realName[app.$store.state.uid].state == 'SUCCESS'){
                    app.btnTxt = '';
                    app.btnType = 'default';
                    app.isdisable = true;
                    $('.name').html('<span>'+data['realName'][app.$store.state.uid].realname+'</span>').css({
                        display:'inline-block',
                        height:'0.88rem',
                        'line-height':'0.88rem'
                    });
                    $('.cardid').html('<span>'+data['realName'][app.$store.state.uid].icardnum+'</span>').css({
                        display:'inline-block',
                        height:'0.88rem',
                        'line-height':'0.88rem'
                    });
                    $('.bankid').html('<span>'+data['realName'][app.$store.state.uid].ccardnum+'</span>').css({
                        display:'inline-block',
                        height:'0.88rem',
                        'line-height':'0.88rem'
                    });
                    $('.bank').html('<span>'+data['realName'][app.$store.state.uid].bank+'</span>').css({
                        display:'inline-block',
                        height:'0.88rem',
                        'line-height':'0.88rem'
                    });
                    $('.openbank').html('<span>'+data['realName'][app.$store.state.uid].openbank+'</span>').css({
                        display:'inline-block',
                        height:'auto',
                        'line-height':'1.5',
                        paddint:'0.2rem 0 0',
                        'text-align':'left'
                    });
                    $('.up').html('<div class="bg" style="width: 4.3rem;height: 3.26rem;margin: 0 auto;background: url('+data['realName'][app.$store.state.uid].icardfurl+') no-repeat center center / 4.3rem 3.26rem;"></div>').click(function(){
                        ImagePreview([data['realName'][app.$store.state.uid].icardfurl])
                    });
                    app.ismask = true;
                    app.is_card = false;
                    app.is_bank = false;
                }else if(data.realName[app.$store.state.uid].state == 'FAILD'){
                    app.$dialog.alert({
                        title:'',
                        message:''
                    }).then(()=>{
                        app.btnTxt = '';
                        app.btnType = 'default';
                        app.is_card = true;
                        app.is_bank = true;
                        app.ismask = true;
                    })
                }
            },function(code,data){
                if(code == 12){
                    app.is_card = true;
                    app.is_bank = true;
                    app.ismask = true;
                    app.Init(app)
                }
            })
        },
        // 
        Init(app){
            TD_Request('us','rnamesx',{uid:app.$store.state.uid},function(code,data){
                console.log(data)
                app.uploadData = data;

            },function(code,data){
                console.log(data);
                if(code == 37){
                    app.$dialog.alert({
                        title:'',
                        message:''
                    })
                }
            })
        },
        // 
        upload(app,updata){
            console.log(app.fileObj)
            WebApp.UploadWithSDK(updata.uptoken,updata.upurl,app.fileObj,updata.filename.id_f,function(res){
                app.matchid(app)
            })
        },
        // 
        submit(){
            if(this.realname == ''){
                this.$dialog.alert({
                    title:'',
                    message:''
                })
            } else if(this.idcard_num == ''){
                this.$dialog.alert({
                    title:'',
                    message:''
                })
            }else if(this.bankcard == ''){
                this.$dialog.alert({
                    title:'',
                    message:''
                })
            }else if(this.bank == ''){
                this.$dialog.alert({
                    title:'',
                    message:''
                })
            }else if(this.openbank == ''){
                this.$dialog.alert({
                    title:'',
                    message:''
                })
            } else if(this.src == ''){
                this.$dialog.alert({
                    title:'',
                    message:''
                })
            } else if(!(/(^\d{15}$)|(^\d{18}$)|(^\d{17}(\d|X|x)$)/).test(this.idcard_num)) {
                this.$dialog.alert({
                    title:'',
                    message:''
                })
            } else {
                this.upload(this,this.uploadData);
            }
        },
        matchid(app){
            TD_Request('us','rnamefx',{uid:app.$store.state.uid,realname:app.realname,ccardnum:app.bankcard,icardnum:app.idcard_num,bank:app.bank,openbank:app.openbank},function(code,data){
                console.log(data)
                app.$toast.success('');
                setTimeout(()=>{
                    app.$router.push('/')
                },1000)
            },function(code,data){
                app.$toast.fail('');
                setTimeout(()=>{
                    app.$router.replace('/refesh')
                },1000)
            })
        }
    },
    watch:{
        realname(data){
            var str = data.replace(/\ +/g,"");
            var str = str.replace(/[\r\n]/g,"");
            this.realname = str;
        },
        idcard_num(data){
            var str = data.replace(/\ +/g,"");
            var str = str.replace(/[\r\n]/g,"");
            if(str.length == 18){
                this.idcard_num = str.substr(0,18);
            }
        },
        bankcard(data){
            var str = data.replace(/\ +/g,"");
            var str = str.replace(/[\r\n]/g,"");
            this.bankcard = str;
        },
        openbank(data){
            var str = data.replace(/\ +/g,"");
            var str = str.replace(/[\r\n]/g,"");
            this.openbank = str;
        }
    }
}
</script>

<style lang="less" scoped>
    .auth{
        width: 100%;
        background: #f0f0f0;
        .warp{
            background: #fff;
            overflow: hidden;
            .main{
                width: 6.9rem;
                margin: 0 auto;
                padding-top: 0.45rem;
                padding-bottom: 0.3rem;
                .left{
                    font-size: 0.3rem;
                    color: #4c4c4c;
                    display: inline-block;
                    height: 0.88rem;
                    line-height: 0.88rem;
                }
                .right{
                    font-size: 0.3rem;
                    color: #b2b2b2;
                    .van-field__control{
                        text-align: right;
                    }
                }
                .upload{
                    height: 1rem;
                    .read{
                        width: 100%;
                    }
                }
                .tip{
                    margin-bottom: 0.4rem;
                    .title{
                        font-size: 0.3rem;
                        margin-top: 0.3rem;
                        color: #4c4c4c;
                    }
                    .msg{
                        font-size: 0.24rem;
                        color: #b2b2b2;
                    }
                }
                .bg{
                    width: 4.3rem;
                    height: 3.26rem;
                    margin: 0 auto;
                    background: url(http://tdream.antit.top/image/idf.png) no-repeat center center / 4.3rem 3.26rem;
                }
            }
        }
        .btn{
            text-align: center;
            margin: 1rem auto 0;
            width: 6.6rem;
            padding-bottom: 0.4rem;
        }
        .van-field__control:disabled{
            color:#000;
        }
    }
