<template>
    <div class="clock" :style="{overflow:ispay == true ? '' : 'auto'}">
        <action v-if="issshow"/>
        <pay-info :feed="feedback" v-if="ispay"/>
    </div>
</template>

<script>
import action from '@/components/clock/Action'
import payInfo from '@/components/index/payInfo/payClock'
export default {
    name:'clock',
    components:{
        action,
        payInfo,
    },
    data(){
        return {
            isshow:true,
            actives:1,
            ispay:false,
            userInfo:'',//
            tabbar:'',
            contracts:'',//
            show:false,//
            payCancel:false,//
            cid:'',// id
            desc:'',//
            title:'',//
            contractType:'',//
            // checkArr:[],//
            price:'',//
            // bill:'',//
            day:'',//
            warm:'',//
            status:null,
            theme:'',//
            isshow:false,//
            custom:'',//
            value:'',//
            cattention:'',//
            feedback:'',//
            order:'',//
            orders:'',//
            cPerson:'',
            cAttendence:'',
            issshow:false,
        }
    },
    created(){
        if(Options.GetUserInfo() == null){
            window.location.href = 'http://tinydream.ivkcld.cn/TInydreamWeb/dream/index.html?time='+new Date().getTime()+'&type=clock'
        }
        this.Register(this,JSON.parse(GetStorage('userInfo')))
    },
    methods:{
        Register(self,userInfo){
            TD_Request('us','enter',{
                uid:userInfo.openid,
                nickname:userInfo.nickname,
                headicon:userInfo.headimgurl
            },function(code,data){
                console.log(data)
                WebApp.JSAPI.InitShare({
                    title:self.userInfo.nickname+'',
                    desc:"",
                    link:'http://tinydream.ivkcld.cn/TinydreamWeb/dream/dist/html/share.html?time='+new Date().getTime()+'&type=clock&state=no',
                    imgUrl:"https://tdream.antit.top/image/miniLogo.jpg"
                });
                self.Orders(self,userInfo.openid)
            },function(code,data){
                console.log(data)
            })
        },
        Orders(self,uid){
            TD_Request('op','eomp',{uid:uid},function(code,data){
                self.ispay = true;
                console.log(data)
                self.feedback = data.feedback;
                var order = data.orders.shift();
                self.cPerson = data.cPerson;
                self.cAttendence = data.cAttendence;
                self.order = order;
                data.orders.push(order);
                self.orders = data.orders;
                self.$toast.clear()
                self.loop(self)
                console.log(data.orders);
            },function(code,data){
                console.log(data)
                if(code == 82){
                    self.issshow = true;
                }
            })
        },
        loop(self){
            setInterval(function(){
                var order = self.orders.shift();
                self.order = order;
                self.orders.push(order)
            },3000)
        }
    }
}
</script>

<style lang="less" scoped>
    .clock{
        background: #f0f0f0;
        width: 100%;
        height: 100%;
        // overflow: scroll;
    }
</style>
