<template>
    <div class="issue">
        <div class="main">
            <div class="pack">
                <div class="warp">
                    <van-col span="12">
                        <p class="title"></p>
                    </van-col>
                    <van-col span="12" style="text-align:right" @click="selectPid">
                        <span style="color:rgb(153, 153, 153)">{{pid}}&nbsp;</span>
                        <van-icon name="arrow-down" style="font-size:0.3rem" @click.stop="selectPid"></van-icon>
                        <span class="last">
                            
                        </span>
                    </van-col>
                </div>
            </div>
            <div class="pack">
                <div class="warp">
                    <van-col span="12">
                        <p class="title"></p>
                    </van-col>
                    <van-col span="12" style="text-align:right">
                        <input type="number" class="people" v-model="pcount" placeholder="" style="width: 80%;text-align: right;color: #999;font-size: 0.3rem;">
                        <span class="last">
                            
                        </span>
                    </van-col>
                </div>
            </div>
            <p class="tip">
                11
            </p>
            <div class="textarea">
                <input type="text" placeholder="" v-model="content" class="blessing">
            </div>
            <div class="money">
                <span></span>
                <span style="font-style:normal">{{pcount == '' ? 0 : pcount * 5}}</span>
            </div>
            <div class="btn">
                <van-button round size="large" style="color: white;font-size: 0.36rem;background-color: red;border: none;" @click="pay"></van-button>
            </div>
            <p class="msg">* 24</p>
        </div>
        <van-actionsheet
            v-model="show"
            :actions="action"
            cancel-text=""
            @select="onSelect"
            @cancel="onCancel"
        />
    </div>
</template>

<script>
export default {
    name:'issue',
    data () {
        return {
            show:false,
            action:[],//
            pid:'',// id
            pcount:'',//
            content:'',//
        }
    },
    created(){
        this.getPid(this)
    },
    methods:{
        selectPid(){
            this.show = true;
        },
        // 
        getPid(app){
            TD_Request('ds','plistr',{seek:0,count:100,redpack:true},function(code,data){
                console.log(data)
                $.each(data.Pools,function(index,item){
                    if(item.state == 'RUNNING'){
                        app.action.push({name:item.pid});
                    }
                })
                console.log(app.action)
                app.pid = app.action[0].name;
            },function(code,data){
                console.log(data)
            })
        },
        onSelect(item){
            this.show = false;
            this.pid = item.name;
        },
        onCancel(){
            this.show = false;
        },
        // 
        pay(){
            if(this.pid == ''){
                this.$dialog.alert({
                    title:'',
                    message:''
                })
            }else if(this.pcount == ''){
                this.$dialog.alert({
                    title:'',
                    message:''
                })
            }else if(typeof this.pcount != 'number'){
                console.log(typeof this.pcount != 'number')
                this.$dialog.alert({
                    title:'',
                    message:''
                })
            }else {
                this.rpPay(this)
            }
        },
        // 
        rpPay(app){
            if(this.content == ''){
                this.content = ''
            }
            TD_Request('rp','crp',{pid:app.pid,rcount:app.pcount,content:app.content,bill:0.01 * 100 * app.pcount,uid:app.$store.state.uid},function(code,data){
                console.log(data)
                app.wxpay(app,data);
            },function(code,data){
                if(code == 11){
                    app.$dialog.alert({
                        title:'',
                        message:''
                    }).then(()=>{
                        SaveStorage('type','issue');
                        app.$router.push('/phone')
                    })
                }else if(code == 5){
                    app.$dialog.alert({
                        title:'',
                        message:''
                    })
                }else if(code == 67){
                    app.$dialog.alert({
                        title:'',
                        message:'200'
                    })
                }else if(code ==69){
                    app.$dialog.alert({
                        title:'',
                        message:''
                    })
                }else if(code == 68){
                    app.$dialog.alert({
                        title:'',
                        message:''
                    })
                }else if(code == 70){
                    app.$dialog.alert({
                        title:'',
                        message:''
                    })
                }
            })
        },
        // &
        wxpay(app,result){
            WeixinJSBridge.invoke(
            'getBrandWCPayRequest', {
            "appId":result.order.appId,     //     
            "timeStamp":result.order.timeStamp,         //1970     
            "nonceStr":result.order.nonceStr, //     
            "package":result.order.package,     
            "signType":result.order.signType,         //     
            "paySign":result.order.paySign // 
            },function(res){
            if(res.err_msg == "get_brand_wcpay_request:ok" ){
            // ,
                    //res.err_msgok
                app.paySuccess(app,result.rid)
            } else if(res.err_msg == "get_brand_wcpay_request:cancel"){
                app.$toast.fail('');
                app.content = '';
            }
        });
        },
        // 
        paySuccess(app,rid){
            TD_Request('rp','cprs',{uid:app.$store.state.uid,rid:rid},function(code,data){
                console.log(data)
                app.$router.push('/rpshare/'+rid);
            },function(code,data){
                console.log(data)
            })
        }
    },
    watch:{
        pcount(data){
            this.pcount = parseInt(data)
            if(data > 200){
                this.$dialog.alert({
                    title:'',
                    message:'200'
                }).then(()=>{
                    this.pcount=''
                })
            }
        },
        content(data){
            if(data.length == 10){
                this.content = data.substr(0.10);
            }
        }
    }
}
</script>

